
.swiper{position:relative;overflow:hidden
}
.swiper .swiper-wrap{display:-webkit-box;display:-moz-box;display:-o-box;display:-ms-flexbox;display:flex;width:100%;height:100%;transition:all .4s ease
}
.swiper .swiper-wrap>div{overflow:hidden;flex-shrink:0;width:100%;height:100%
}
.swiper .btn-change{display:none
}
.swiper.horizontal .swiper-wrap{flex-direction:row
}
.swiper.dragging .swiper-wrap{transition:none
}
.swiper .swiper-pagination{position:absolute
}
.swiper .swiper-pagination .swiper-pagination-bullet{width:8px;height:8px;border-radius:50%;background-color:#000000;opacity:.2;cursor:pointer
}
.swiper .swiper-pagination .swiper-pagination-bullet.active{background:#007aff;opacity:1
}
.swiper.horizontal .swiper-pagination{bottom:10px;width:100%;text-align:center
}
.swiper.horizontal .swiper-pagination .swiper-pagination-bullet{display:inline-block;margin:0 3px
}


