
.title-mask{
  opacity: 0.8;
  background-color: black;
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: 1.25rem;/* 50px */
  line-height: 1.25rem;/* 50px */
  text-overflow: ellipsis;
  width: 100%;
  color: #fff
}
.button-prev{
  position: absolute;
  background: transparent;
  left: 15%;
  top: 40%;
  color: red;
  font-size: 40px;
  cursor: pointer;
  z-index: 99;
}
.button-next{
  position: absolute;
  background: transparent;
  right: 15%;
  top: 40%;
  color: red;
  font-size: 40px;
  cursor: pointer;
  z-index: 99;
}
html {
  height: 100%;
}
body {
  height: 100%;
  max-width: 500px;
  margin: 0 auto
}
#app {
  color: #2c3e50;
  max-width: 600px;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
  width: 100%;
  margin: 0 auto
}
.swiper {
  height: 150px;
  margin-bottom: 80px;
  width: 100%;
  max-width: 350px;
  margin: 0 auto
}

/*change the pagination u like */
.swiper-pagination-bullet.active {
  background: #FB7299 !important;
}
.swiper-pagination-bullet {
  background: #fff !important;
  opacity: 1 !important;
  margin: 0 2px !important;
  z-index: 9;
}
.horizontal .swiper-pagination {
  margin-right: 10px !important;
  width: 95% !important;
  text-align: right !important;
}




